import API_ENDPOINT from '../globals/api-endpoint';

class AnimeSource {
  static async season() {
    const response = await fetch(API_ENDPOINT.SEASON);
    const responseJson = await response.json();
    return responseJson.data;
  }

  static async schedule() {
    const response = await fetch(API_ENDPOINT.SCHEDULE);
    const responseJson = await response.json();
    return responseJson.data;
  }

  static async genre() {
    const response = await fetch(API_ENDPOINT.GENRE);
    const responseJson = await response.json();
    return responseJson.data;
  }

  static async topAnime() {
    const response = await fetch(API_ENDPOINT.TOP);
    const responseJson = await response.json();
    return responseJson.data;
  }

  static async detailAnime(id) {
    const response = await fetch(API_ENDPOINT.DETAIL(id));
    const responseJson = await response.json();
    return responseJson.data();
  }

  static async mondayAnime() {
    const response = await fetch(API_ENDPOINT.MONDAY);
    const responseJson = await response.json();
    return responseJson.data;
  }
}

export default AnimeSource;
