import AnimeSource from '../../data/anime-source';
import { createGenreItemTemplate } from '../templates/template-creator';

const Genre = {
  async render() {
    return `
    <div class="container">
    <h1 class="py-4 judul text-center">Genre Anime</h1>
    <div id="genres" class="genres row row-cols-2 row-cols-lg-4 g-2 g-lg-3">
    </div>
    `;
  },

  async afterRender() {
    const genres = await AnimeSource.genre();
    const genresContainer = document.querySelector('#genres');
    genres.forEach((genre) => {
      genresContainer.innerHTML += createGenreItemTemplate(genre);
    });
  },
};

export default Genre;
