import AnimeSource from '../../data/anime-source';
import { createTopAnimeItemTemplate } from '../templates/template-creator';

const Top = {
  async render() {
    return `
    <div class="container">
    <h1 class="my-4 judul text-center">Anime Terbaik</h1>
    <div class="row">
      <div id="tops" class="tops"></div>
    </div>
  </div>
    `;
  },

  async afterRender() {
    const top = await AnimeSource.topAnime();
    const topContainer = document.querySelector('#tops');
    top.forEach((topAnime) => {
      topContainer.innerHTML += createTopAnimeItemTemplate(topAnime);
    });
  },
};

export default Top;
