const createTopAnimeItemTemplate = (topAnime) => `
<div class="col-12 mb-3">
    <div class="card border-0">
      <div class="card-body">
        <div class="row align-items-center justify-content-center">
          <div class="col-lg-1 col-md-2 col-sm-12 text-center">
            <img src="${topAnime.images.webp.image_url}" width="400rem" class="rounded img-fluid" alt="">
          </div>
          <div class="col-lg-6 col-md-6 col-sm-12 mt-3 text-center flex-grow-1">
            <div class="row justify-content-end me-4">
              <div class="col-lg-4 col-md-4 col-sm-12 fs-6">
                <p class="pt-2 fs-5">
                  <a href="/detail.html" class="text-decoration-none anime">${topAnime.title}</a>
                </p>
                <div class="row mt-1 text-center">
                  <div class="fs-6 col-3"><i class="bi bi-person-fill"></i><p>${topAnime.members}</p></div>
                  <div class="fs-6 col-3"><i class="bi bi-camera-video-fill"></i><p>${topAnime.type}</p></div>
                  <div class="fs-6 col-3"><i class="bi bi-star-fill"></i><p>${topAnime.score}</p></div>
                  <div class="fs-6 col-3"><i class="bi bi-collection-fill"></i><p>${topAnime.episodes}</p></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</div>
`;

const createGenreItemTemplate = (genre) => `
  <div>
    <a href="#" class="text-decoration-none">
      <div class="card">
          <div class="card-body">
              <div class="row">
                  <div class="col-10">${genre.name}</div>
                  <div class="col-2"><i class="bi bi-chevron-right"></i></div>
              </div>
          </div>
      </div>
    </a>
  </div>
`;

const createSeasonItemTemplate = (season) => `
<div>
<div class="card text-center w-100 shadow-lg border-0" style="width: 18rem;">
<img src="${season.images.webp.image_url}" class="card-img-top" alt="Anime Cover">
  <div class="card-body">
    <a href="/detail.html" class="h6 card-title text-decoration-none anime">${season.title}</a>
    <hr>
    <div class="row row-cols-4 row-cols-sm-2 row-cols-md-2 mt-1">
      <div class="fs-6"><i class="bi bi-person-fill"></i><p>${season.members}</p></div>
      <div class="fs-6"><i class="bi bi-camera-video-fill"></i><p>${season.popularity}</p></div>
      <div class="fs-6"><i class="bi bi-star-fill"></i><p>${season.score}</p></div>
      <div class="fs-6"><i class="bi bi-collection-fill"></i><p>${season.favorites}</p></div>
    </div>
  </div>
</div>
</div>
`;

const createMondayAnimeItemTemplate = (monday) => `
                    <div class="card border-0">
                      <div class="card-body">
                        <div class="row align-items-center d-flex justify-content-center">
                          <div class="col-lg-3 col-md-6 col-sm-12 text-center">
                            <img src="${monday.images.webp.image_url}" width="400rem" class="rounded img-fluid" alt="">
                          </div>
                          <div class="col-lg-9 col-md-6 col-sm-12 fs-6">
                            <p class="pt-2 fs-5">
                              <a href="/detail.html" class="text-decoration-none anime">${monday.title}</a>
                            </p>
                            <div class="row mt-1 text-center">
                              <div class="fs-6 col-4"><i class="bi bi-person-fill"></i><p>${monday.members}</p></div>
                              <div class="fs-6 col-4"><i class="bi bi-star-fill"></i><p>${monday.score}</p></div>
                              <div class="fs-6 col-4"><i class="bi bi-clock-fill"></i><p>${monday.broadcast.time}</p></div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
`;

export {
  createGenreItemTemplate,
  createSeasonItemTemplate,
  createTopAnimeItemTemplate,
  createMondayAnimeItemTemplate,
};
