import AnimeSource from '../../data/anime-source';
import { createMondayAnimeItemTemplate } from '../templates/template-creator';

const Schedule = {
  async render() {
    return `
    <div class="container">
    <h1 id="merah" class="py-4 text-center">Jadwal Anime</h1>
    <div class="row mt-3">
      <h6 id="merah"><i class="bi bi-clock-fill"></i> Waktu yang ditampilkan adalah jadwal waktu perdana di Jepang</h6>
      <div class="accordion" id="accordionExample">
        <div class="accordion-item border-0">
          <h2 class="accordion-header" id="headingOne">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne">
              Senin
            </button>
          </h2>
          <div id="collapseOne" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div class="accordion-body">
              <div id="mondays" class="mondays row row-cols-2 row-cols-lg-3 g-2 g-lg-3"></div>
            </div>
          </div>
        </div>
        <div class="accordion-item border-0">
          <h2 class="accordion-header" id="headingTwo">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo">
              Selasa
            </button>
          </h2>
          <div id="collapseTwo" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div class="accordion-body">
              <div class="row">
                
              </div>
            </div>
          </div>
        </div>
        <div class="accordion-item border-0">
          <h2 class="accordion-header" id="headingThree">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree">
              Rabu
            </button>
          </h2>
          <div id="collapseThree" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div class="accordion-body">
              <div class="row">
                
              </div>
            </div>
          </div>
        </div>
        <div class="accordion-item border-0">
          <h2 class="accordion-header" id="headingFour">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseFour">
              Kamis
            </button>
          </h2>
          <div id="collapseFour" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div class="accordion-body">
              <div class="row">
                
              </div>
            </div>
          </div>
        </div>
        <div class="accordion-item border-0">
          <h2 class="accordion-header" id="headingFive">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseFive">
              Jumat
            </button>
          </h2>
          <div id="collapseFive" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div class="accordion-body">
              
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
    `;
  },

  async afterRender() {
    const mondays = await AnimeSource.mondayAnime();
    const mondaysContainer = document.querySelector('#mondays');
    mondays.forEach((monday) => {
      mondaysContainer.innerHTML += createMondayAnimeItemTemplate(monday);
    });
  },
};

export default Schedule;
