import AnimeSource from '../../data/anime-source';
import { createSeasonItemTemplate } from '../templates/template-creator';

const Season = {
  async render() {
    return `
    <div class="container">
    <h1 class="my-4 judul text-center">Summer 2023</h1>
    <div class="row">
    <div id="seasons" class="seasons row row-cols-2 row-cols-lg-5 g-2 g-lg-3">
    </div>
  </div>
    `;
  },

  async afterRender() {
    const seasons = await AnimeSource.season();
    const seasonsContainer = document.querySelector('#seasons');
    seasons.forEach((season) => {
      seasonsContainer.innerHTML += createSeasonItemTemplate(season);
    });
  },
};

export default Season;
